@import url(https://fonts.googleapis.com/css?family=Raleway);
* {
  font-family: "Raleway", sans-serif;
}
body{
  background-image: linear-gradient(to right, rgb(103, 103, 131), #bec3c5);

}
.container {
  position: absolute;
  width: 90%;
  height: 800px;
  border: 1px solid rgba(224, 188, 188, 0.2);
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  padding: 10px;
  box-sizing: border-box;
  top: 30%;
  bottom: 0px;
  left: 0px;
  right: 0px;
  margin: auto;
  background:rgb(49, 42, 58);
}

.title {
  position: relative;
  background-color: rgba(124, 152, 206, 0.788);
  text-shadow: 1px 1px 2px black, 0 0 25px blue, 0 0 5px darkblue;

  color: white;
  font-family: Apple Chancery, cursive	;
  padding: 20px;
  font-size: 2.8em;
    border-radius: 5px;
}

.question {
  font-size: 1.2em;
  font-weight: bold;
  margin-bottom: 8px;
}

.questionBox {
  position: relative;
  border: 1px solid rgba(0, 0, 0, 0.2);
  margin-top: 5px;
  padding: 15px;
  box-sizing: border-box;
  background-color: rgb(250, 250, 250);
}

.answerBtn {
  position: relative;
  padding: 15px;
  background-color: rgb(107, 112, 194);
  display: inline-block;
  margin: 7px;
  outline: none;
  border: none;
  font-size: 1em;
  color: rgb(255, 255, 255);
  font-weight: bold;
  border-radius: 5px;
  cursor: pointer;
}

.score-board {

  position: absolute;
  width: 90%;
  height: 700px;
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.1);
  padding: 10px;
  box-sizing: border-box;
  top: 0px;
  bottom: 20px;
  left: 0px;
  right: 0px;
  margin: auto;
  display: flex;
  justify-content: center;
}


.score {
  position: absolute;
  width: 1024px;
  font-size: 3em;
  top: 250px;
  bottom: 30px;
  text-align: center;
  color:white;
}

.playBtn {
  position: absolute;
  font-size: 1.4em;
  padding: 10px;
  top: 480px;
  background-color: rgb(46, 182, 46);
  color: rgb(255, 255, 255);
  width: 150px;
  left: 0px;
  right: 0px;
  margin: 4em;
  outline: none;
  cursor: pointer;
}

